.gallery-2 {
  @include list-style-none();
  display: flex;
  flex-wrap: wrap;
  margin: 0 -11px;
}

.gallery-2__item {
  $big-img-padding: 340px;
  $corner-x-position: 265px;

  position: relative;
  width: calc(50% - 11px * 2);
  height: 513px;
  padding: 0 120px 0 0;
  margin: 0 11px 100px;

  &:nth-child(10n + 3),
  &:nth-child(10n + 7) {
    margin-top: 190px;
  }

  &:nth-child(10n + 4),
  &:nth-child(10n) {
    margin-top: -190px;
  }

  &:nth-child(5n + 1) {
    position: relative;
    width: 100%;
    padding: 0 $big-img-padding 0 0;
    margin-top: 0;

    & .gallery-2__info {
      width: 700px;
      padding-right: 120px;
    }

    &::after {
      position: absolute;
      content: "";
      top: calc(100% + 20px);
      right: $corner-x-position;
      width: 56px;
      height: 56px;

      background: url("/assets/img/icon/corner.svg");
    }

    &:nth-child(odd) {
      &::after {
        transform: rotate(-90deg);
      }
    }

    &:nth-child(even) {
      padding: 0 0 0 $big-img-padding;

      & .gallery-2__info-wrapper {
        left: 0;
      }

      &::after {
        right: 0;
        left: $corner-x-position;
      }
    }
  }
}

.gallery-2__info-wrapper {
  position: absolute;
  z-index: 1;
  top: 50%;
  right: 0;
  overflow: hidden;
  transform: translateY(-50%);
}

.gallery-2__info {
  width: 338px;
  padding: 80px 0 80px 60px;

  background-color: $white;
}

.gallery-2__description {
  @include font-options(20px, 24px, 300);
  margin: 0;
}

.gallery-2__img-wrapper {
  width: 100%;
  height: 100%;
  overflow: hidden;

  &:hover {
    @include gallery-button-wraper-hover();
  }

  & img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.gallery-2__img-container {
  width: 100%;
  height: 100%;
  overflow: hidden
}

.gallery-2__button {
  @include gallery-button(100%, 50%);
}
