@import "../../includes/elements/sections/gallery-2";

.index-service {
  width: 100%;
}

.index-service__container {
  position: relative;
  padding-bottom: 20px;
  overflow: hidden;
}

.index-service__description {
  @include font-options(20px, 24px, 300);
  margin: 0 0 60px;

  & span {
    display: block;
  }
}

.index-service__decorative-text-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.index-service__decorative-text {
  position: absolute;
  z-index: -1;
  display: block;

  @include title-decorative();

  &:first-child {
    top: 2035px;
    left: $container-padding-left-right;

    &::before {
      content: "Service";
    }
  }
}

.index-service__gallery .gallery-2__img-curtain {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;

  background-color: $white;
}
.sub-title-mobile {
  display: none;
}


@media (max-width: 1800px) {
  .index-service__container {
    padding: 120px 40px;
  }
}

@media (max-width: 1400px) {
  // .gallery-2__info-wrapper {
  //   display: none;
  // }
  .gallery-2__info {
    display: none;
  }
  .gallery-2__description {
    display: none;
  }
  .gallery-2__item:nth-child(5n+1) {
    padding: 0;
  }
  .gallery-2__item:nth-child(3) {
    margin-top: 0;
  }
  .gallery-2__item:nth-child(4) {
    margin-top: 0;
  }
  .index-service__decorative-text {
    display: none;
  }
  .gallery-2__item:nth-child(5n+1):nth-child(even) {
    padding: 0;
}
.gallery-2__item:nth-child(10n+3), .gallery-2__item:nth-child(10n+7) {
  margin-top: 0;
}
  .gallery-2__item {
    padding: 0;
  }
  .gallery-2__item:nth-child(5n+1)::after {
    display: none;
  }
  .sub-title-mobile {
    display: block;
    margin: 0;
    margin-top: 10px;
  }
}

@media (max-width: 1200px) {
  .gallery-2__item {
    height: 360px;
  }
}

@media (max-width: 725px) {
  .gallery-2__item {
    height: 260px;
  }
  .sub-title-mobile {
    font-size: 18px;
  }
}

@media (max-width: 525px) {
  .gallery-2__item {
    height: 200px;
    margin: 0px 11px 64px;

  }
  .index-service__title {
    font-size: 24px;
    margin-bottom: 16px;
  }
  .index-service__description {
    font-size: 16px;
    margin-bottom: 26px;
  }
}

@media (max-width: 420px) {
  .gallery-2__item {
    height: 110px;
  }
  .index-service__container {
    padding: 0px 10px;
  }
}