.index-contacts {
  width: 100%;
  overflow: hidden;
}

.index-contacts__bg {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;

  background:
    url("/assets/img/index/pool.jpg"),
    linear-gradient(#6d8a07, #74b0d6);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.index-contacts__curtain {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;

  background-color: $white;
}
.index-contacts__info {
  .decorative-section__wrapper {
    padding-bottom: 0;
    // padding-top: 174px;
  }
  .decorative-section__decorative-text {
    order: 3;
    opacity: 1!important;
    transform: translate(117px, 0px)!important;
    line-height: 460px;
  }
}
.decorative-section__decorative-text2 {
  display: none;
}


@media (max-width: 1900px) {
  .index-contacts__info { 
    .decorative-section__decorative-text {
      font-size: 345px;
    }
  }
}

@media (max-width: 1800px) {
  .index-contacts__info {
    .decorative-section__decorative-text {
      transform: translate(128px, 0px)!important;
      font-size: 250px;
      bottom: -109px;
    }
  }
}

@media (max-width: 1400px) {
  .index-contacts__info {
    .decorative-section__decorative-text {
      font-size: 190px;
      bottom: -137px;
    }
  }
}

@media (max-width: 1200px) {
  .index-contacts__info {
    .decorative-section__decorative-text {
      font-size: 158px;
      bottom: -152px;
      transform: translate(112px, 0px);
    }
  }
}



@media (max-width: 960px) {
  .index-contacts__info {
    .decorative-section__decorative-text {
      display: none;
    }
  }
  .decorative-section__decorative-text2 {
    display: block;
    font-family: Cannu;
    font-style: normal;
    font-weight: normal;
    font-size: 70px;
    line-height: 84px;
    color: #E3E0CF;
    transform: rotate(-90deg);
    position: absolute;
    right: -84px;
    top: 30%;
    // transform: translateY(-50%);
  }
  .decorative-section--index-contacts .decorative-section__wrapper {
    padding: 120px 10px;
  }
}


@media (max-width: 562px) {
  .index-contacts__bg {
    display: none;
  }
  .decorative-section__decorative-text2 {
    top: 58px;
  }
  .index-contacts__info {
      .decorative-section--index-contacts .decorative-section__wrapper {
        padding: 20px 20px;
    }
      .decorative-section__decorative-text {
        display: none;
      }
      .decorative-section__wrapper {
        background: none;
        backdrop-filter: none;
        width: 90%;
      }
      .decorative-section__text {
        margin: 0;
        margin-bottom: 20px;
      }
      .decorative-section__title {
        color: #000;
        font-weight: 500;
        font-size: 36px;
        margin-top: 40px;
      }
      .decorative-section__text {
        color: #000;
        font-size: 24px;
      }
    
      .decorative-section__button {
        max-width: 165px;
        padding: 10px 10px 10px;
        border: 1px solid #000;
        color:#000
      }
  }
 
}
@media (max-width: 420px) {
  .index-contacts__info {
    .decorative-section__title {
      font-size: 24px;
    }
    .decorative-section__text {
      font-size: 16px;
    }
    .decorative-section__button {
      width: 180px;
      height: 50px;
      padding: 0;
      margin: 0;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .decorative-section__button-wrapper {
      margin: 0;
    }
    .decorative-section__wrapper {
      width: 70%;
    }
  }
  
}
