$margin-left: 187px;
$padding-left: 112px;


.decorative-section {
  position: relative;
  width: 100%;
  height: 100%;

  overflow: hidden;

  &--index-about {
    & .decorative-section__title {
      margin-bottom: -70px;
    }

    & .decorative-section__decorative-text {
      opacity: 1!important;
      // top: 181px;
      // left: $padding-left;
    }
  }

  &--index-contacts {
    .decorative-section__wrapper {
      padding-top: 174px;
      padding-bottom: 237px;
    }

    & .decorative-section__decorative-text {
      bottom: -60px;
      right: 0;
    }
  }
}

.decorative-section__container {
  height: 100%;
  padding-top: 0;
  padding-bottom: 0;
}

.decorative-section__wrapper {
  position: static;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 368px;
  height: 100%;
  margin: 0 $margin-left;
  padding: 150px 40px;
  color: $white;

  background-color: rgba($white, .1);
  backdrop-filter: blur(20px);
}

.decorative-section__title {
  &--isHide {
    display: none;
  }
}

.decorative-section__text {
  margin: 0 0 26px;
  margin-top: 33px;

  @include font-options(20px, 24px, 300);

  &--isHide {
    display: none;
  }
}

.decorative-section__decorative-text {
  // position: absolute;
  width: max-content;
  margin: 0;

  @include font-options(350px, 350px, 400);
  font-family: "Cannu", "Times New Roman", serif;
  color: transparent;

  background: linear-gradient($white 50%, rgba($white, 0) 130%);
  background-clip: text;
  pointer-events: none;
}

.decorative-section__button {
  max-width: 230px;
}


@media (max-width: 1900px) {
  .decorative-section__wrapper {
    margin: 0;
  }
  .decorative-section__decorative-text {
    font-size: 240px;
  }
}
@media (max-width: 1400px) {
  .decorative-section__wrapper {
    width: 260px;

  }
  .decorative-section__decorative-text {
    font-size: 166px;
    line-height: 250px;
  }
  .decorative-section__text {
    margin-top: 0;
  }

}

@media (max-width: 1200px) {
  .decorative-section__container   {
    padding: 0 20px;
  }
  .decorative-section__wrapper {
    padding: 150px 15px;
    width: 220px;
  }
  .decorative-section__decorative-text {
    font-size: 150px;
    line-height: 220px;
  }
}

@media (max-width: 960px) {
  .decorative-section__wrapper {
    padding: 150px 10px;
    width: 195px;
  }
  .decorative-section__container {
    padding: 0;
  }
  .decorative-section__title {
    font-size: 22px;
  }
  .decorative-section__decorative-text {
    font-size: 175px;
    line-height: 240px;
  }
}
@media (max-width: 720px) {
  .decorative-section__wrapper {
    padding: 0 10px;
    justify-content: flex-start;
  }
  .decorative-section--index-about .decorative-section__title {
    margin-top: 20px;
  }
}

@media (max-width: 525px) {
  .decorative-section__wrapper {
    padding: 0 10px;
    width: 148px;
    justify-content: flex-start;
  }
  .decorative-section--index-about .decorative-section__title {
    font-size: 20px;
    margin-bottom: -33px;
  }
  .decorative-section__decorative-text {
    font-size: 105px;
    line-height: 132px;
  }
  .decorative-section__text {
    font-size: 16px;
    min-width: 250px;
    margin-top: 13px;
  }
  .decorative-section__button-wrapper {
    width: 180px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #fff;
    margin-left: 5px;
  }
}