@import "../../includes/elements/sections/gallery-1";


.index-rooms {
  width: 100%;
  overflow: hidden;
//   display: none;
}

.index-rooms__container {
  position: relative;
  display: flex;
  width: 100%;
}

.index-rooms__description {
  position: relative;
  flex-shrink: 0;
  width: 230px;
  height: fit-content;
  margin-right: 130px;
}

.index-rooms__description-list {
  @include list-style-none();
  margin-bottom: 30px;
}

.index-rooms__description-item {
  @include font-options(20px, 32px, 300);
}

.index-rooms__gallery {
  width: 100%;
  padding-bottom: 60px;
}

.index-rooms__description-corner {
  @include corner-icon(calc(100% + 40px), false, false, 0, 0);
}

.index-rooms__gallery-corner {
  @include corner-icon(calc(100% - 455px), 0, false, false, -90deg);
}

.index-rooms__decorative-text-container {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
}

.index-rooms__decorative-text {
  position: absolute;
  z-index: -1;
  display: block;

  @include title-decorative();

  &:first-child {
    bottom: 288px;
    left: $container-padding-left-right;

    &::before {
      content: "Hotel";
    }
  }

  &:nth-child(2) {
    bottom: 98px;
    right: $container-padding-left-right;

    &::after {
      content: "Rooms";
    }
  }
}
.gallery-1__item-slider-content {
  display: none;
}
.title-2 {
  display: none;
}


@media (max-width: 1800px) {
  .index-rooms__container {
    padding: 120px 20px;
  }
  .index-rooms__description {
    margin-right: 22px;
  }
  .gallery-1__item-content {
    width: 400px!important;
  }
  .gallery-1 {
    gap: 0 14px;
  }

}

@media (max-width: 1400px) {
  .index-rooms__description {
    display: none;
  }
  .index-rooms {
    .index-rooms__decorative-text-container {
      display: none;
    }
    .index-rooms__gallery-corner {
      display: none;
    }
  }
}

@media (max-width: 1200px) {
  .gallery-1__item-content {
    width: 290px!important;
  }
  .gallery-1__img-wrapper {
    height: 300px!important;
  }
}

@media (max-width: 960px) {
  .title-2 {
    display: block;
  }
  .index-rooms__container {
    display: block;
  }
  .gallery-1__item-content {
    width: auto!important;
  }
  .gallery-1__img-wrapper {
    height: auto!important;
  }
}

@media (max-width: 768px) {
  .index-rooms__container {
    padding: 20px;
  }
  .gallery-1 {
    display: flex;
    width: max-content;
    flex-flow: row-reverse
  }
  .index-rooms__gallery {
    padding-bottom: 0;
  } 
  .gallery-1__img-wrapper {
    height: 240px!important;
  }
  .gallery-1__item-title {
    text-align: left;
  }
  .gallery-1__item-slider-content {
    display: flex;
    align-items: center;
  }
  .gallery-1__item-slider-content >  div {
    display: flex;
    align-items: center;
  }
  .gallery-1__item-slider-content img {
    width: 20px;
    height: 20px;
    margin-right: 8px;
  }
  .gallery-1__item-slider-content p {
    font-size: 16px;
    color: #000000;
  }
  .border {
    background: #606060;
    width: 4px;
    height: 4px;
    margin: 0 10px;
  }
}

@media (max-width: 525px) {
  .index-rooms {
    background: #e3e0cf;
    transform: translateY(650px);
  }
}
@media (max-width: 420px) {
  .gallery-1__img-wrapper {
    height: 181px!important;
  }
}




