.gallery-1 {
  @include list-style-none();
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0 140px;
}

.gallery-1__item {
  display: block;
  margin: 0;

  &:nth-child(4n - 1) .gallery-1__img-wrapper,
  &:nth-child(4n - 2) .gallery-1__img-wrapper,
  &:last-child .gallery-1__img-wrapper {
    height: 383px;
  }
}

.gallery-1__item-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.gallery-1__item-title {
  margin: 26px 0 0;
  overflow: hidden;

  text-align: right;
  white-space: nowrap;
}

.gallery-1__img-wrapper {
  order: -1;
  position: relative;
  width: 100%;
  height: 540px;

  &:hover {
    @include gallery-button-wraper-hover();
  }

  & img {
    position: relative;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transform: scale(1.3);
  }
}

.gallery-1__img-container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  overflow: hidden;
}


.gallery-1__button {
  @include gallery-button(50%, 0);
}
