.button {
  display: block;
  width: 100%;
  margin: 0;
  padding: 20px 25px 16px;

  @include font-options(16px, 24px, 500);
  text-transform: uppercase;
  text-align: center;

  cursor: pointer;
  transition: all .3s;

  &--black {
    color: $white;
    background-color: $black;
    border: 1px solid $black;

    &:hover,
    &:focus {
      color: $black;
      background-color: transparent;
    }
  }

  &--transparent-white {
    color: $white;
    background-color: transparent;
    border: 1px solid $white;

    &:hover,
    &:focus {
      background-color: $black;
      border-color: $black;
    }
  }

  &--transparent-black {
    color: $black;
    background-color: transparent;
    border: 1px solid $black;

    &:hover,
    &:focus {
      color: $white;
      background-color: $black;
    }
  }

  &:active {
    opacity: .3;
  }
}


// gallery-button
@mixin gallery-button($top: 50%, $right: 0) {
  position: absolute;
  // top: $top;
  // right: $right;

  transform: translate(-50%, -100%);
}
.gallery-2__button {
  z-index: 5;
}
@mixin gallery-button-focus() {
  transform: scale(1);
  opacity: 1;
}

@mixin gallery-button-hover(){
  transform: scale(1.2);
  border: 1px solid $sand2;
}

@mixin gallery-button-active(){
  opacity: .6;
}

@mixin gallery-button-wraper-hover() {
  & .gallery-button {
    @include gallery-button-focus();
  }

  & .gallery-button:hover {
    @include gallery-button-hover();
  }

  & .gallery-button:active {
    @include gallery-button-active();
  }
}

.gallery-button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 154px;
  height: 154px;
  margin: 0;

  background-color: $sand;
  border-radius: 50%;
  border: 1px solid $sand;
  cursor: pointer;
  opacity: 0;
  transform: scale(0);
  transition: .4s;

  &--border {
    border: 1px solid $sand2;
  }

  .gallery-button__icon {
    &::before {
      display: block;
      margin: 0;

      font-size: 42px;
      color: $black;
    }
  }

  &:focus {
    @include gallery-button-focus();
  }

  &:hover {
    @include gallery-button-hover();
  }

  &:active {
    @include gallery-button-active();
  }
}



@media (max-width: 1400px) {
  .button--transparent-white {
    font-size: 14px;
  }
  
}

@media (max-width: 960px) {
  .header__pick-up-room  {
    padding: 0 5px 2px;
  }
  .header__pick-up-room-container{
    display: flex;
    align-items: center;
    height: 40px;
    background: #000;
  }
}
@media (max-width: 720px) {
  .header__pick-up-room-container{
    display: none;
  }
}

@media (max-width: 525px) {
  .button--transparent-white {
    border: none;
  }
}