@import "../../includes/elements/circle-counter";

$description-width: 620px;
$padding-right: 70px;
$white-line-height: 53px;
$circle-counter-margin: 45px;


.index-seasonal {
  width: 100%;
  min-height: 428px;
  margin-bottom: 53px;

  background: $sand;
}

.index-seasonal__container {
  display: flex;
  padding-bottom: 43px;
}

.index-seasonal__info {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 617px;
  padding-right: 71px;
}

.index-seasonal__description {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.index-seasonal__description-list {
  @include list-style-none();
  display: flex;
}

.index-seasonal__description-item {
  flex-shrink: 0;
  width: 100% !important;
}

.index-seasonal__description-item p {
  margin: 0 0 23px;

  &:last-of-type {
    margin-bottom: 26px;
  }
}

.index-seasonal__circle-counter {
  margin-top: auto;
}

.index-seasonal__photos {
  align-self: flex-end;
  flex-shrink: 0;
  position: relative;
  width: 680px + 120px;
  height: 524px;
  margin-bottom: -43px - 53px;
  overflow: hidden;
}

.index-seasonal__photos-list {
  @include list-style-none();
  width: 100%;
  height: 100%;
}

.index-seasonal__photos-item {
  position: absolute;
  top: 0;
  left: 0;
  width: 680px; //100%;
  height: 100%;
  //padding-right: 120px;

  opacity: 0;
  visibility: hidden;

  &.js-isPrev {
    opacity: 1;
    visibility: visible;
    z-index: 1;
    overflow: hidden;
  }

  &.js-isActive {
    opacity: 1;
    visibility: visible;
    z-index: 0;
  }
}

.index-seasonal__img-container {
  width: 100%;
  height: 100%;

  &:hover {
    @include gallery-button-wraper-hover();
  }
}

.index-seasonal__img-wrapper {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: 100%;
  height: 100%;
  overflow: hidden;

  & img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}


.index-seasonal__img-button {
  @include gallery-button(50%, 0);
}

.index-seasonal__slider-controls {
  position: absolute;
  z-index: 2;
  bottom: 0;
  left: 0;
}

.index-seasonal__decorative-text-wrapper {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  width: $container-padding-left-right;
  height: 100%;
  overflow: hidden;
}

.index-seasonal__decorative-text {
  position: relative;
  top: 50%;
  right: 0;
  display: block;
  margin: 0;

  @include font-options(174px, 209px, 400);
  font-family: "Cannu", "Times New Roman", serif;
  color: $white;
  text-transform: lowercase;

  transform-origin: top left;
  transform: rotate(-90deg) translate(-50%, -64px);
}
.index-seasonal__subtitle {
  display: none;
}
.index-seasonal__decorative-text2 {
  display: none;
}

@media (max-width: 1800px) {
  .slider-controls__button {
    width: 92px!important;
    height: 76px!important;
  }
  .index-seasonal__photos {
    width: 450px;
    height: 314px;
    margin-bottom: 0;
  }
  .index-seasonal__photos-item {
    width: 100%;
  }
}

@media (max-width: 1400px) {
  .index-seasonal__photos {
    width: 364px;
    height: 314px;
    margin-bottom: 0;
  }
}

@media (max-width: 1200px) {
  .index-seasonal__description {
    margin-top: 20px;
  }
  .index-seasonal__info {
    max-width: 100%;
  }
  .index-seasonal {
    // background: none;
  }
  .index-seasonal__container {
    display: flex;
    flex-direction: column-reverse;
    padding-bottom: 43px;
    justify-content: center;
    align-items: center;
  }
  .index-seasonal__subtitle {
    display: block;
    width: 100%;
  }
  .index-seasonal__title {
    display: none;
    
  }
  .index-seasonal__circle-counter {
    display: none;
  }
  .index-seasonal__photos { 
    align-self: center;
    width: 100%;
    height: 360px;
  }
  .index-seasonal__decorative-text2 {
    display: block;
    transform-origin: top;
    transform: none;
    top: 0;
    font-size: 147px;
    // color: #E3E0CF;
  }
  .index-seasonal__subtitle h2 {
    font-size: 39px;
  }
  .index-seasonal__decorative-text-wrapper {
    display: none;
  }
  .index-seasonal__container {
    padding: 120px 40px;
  }
  .index-seasonal__container {
    padding: 0px 10px;
  }
  .index-seasonal__hoteltext {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 300px;
  }

}
@media (max-width: 960px) {
  .index-seasonal__hoteltext {
    min-height: 250px;
    align-items: flex-start ;
  }
}

@media (max-width: 692px) {
  .index-seasonal__decorative-text2 {
    font-size: 102px;
  }
}

@media (max-width: 525px) {
  .index-seasonal {
    transform: translateY(-350px);
    background: none;
  }

  .index-seasonal__decorative-text {
    color: #e3e0cf;
  }
  .index-seasonal__hoteltext {
    min-height: 145px;
  }
  .index-seasonal__photos {
    height: 181px;
  }
  .index-seasonal__slider-controls {
    display: none;
  }
  .index-seasonal__subtitle h2 {
    font-size: 24px;
  }
  .index-seasonal__decorative-text2 {
    font-size: 72px;
    line-height: 79px;
  }
  .index-seasonal__photos-list {
    display: flex;
    width: max-content;
  }
  .index-seasonal__photos-item {
    position: relative;
    visibility: unset;
    opacity: 1;
  }
}