.index-about-hotel {
  width: 100%;
}

.index-about-hotel__container {
  overflow: hidden;
}

.index-about-hotel__wrapper {
  position: relative;
  padding-top: 96px;
}

.index-about-hotel__corner {
  position: absolute;
  top: 0;
  left: 0;
  width: 56px;
  height: 56px;

  transform: rotate(90deg);

  &::before {
    font-size: 56px;
    color: $sand;
  }
}

.index-about-hotel__img-wrapper {
  width: 100%;
  height: 680px;
  padding-left: 470px;
  overflow: hidden;
  top: 1px;

  & img {
    width: 100%;
    height: 100%;
    margin-top: -10%;
    object-fit: cover;
  }
}

.index-about-hotel__info-wrapper {
  position: absolute;
  z-index: 1;
  width: 100%;
  max-width: 770px;
  padding: 0 113px 0 0;

  background-color: $white;
}

.index-about-hotel__info-list {
  @include list-style-none();
}

.index-about-hotel__info-item {
  display: block;
  margin: 0 0 30px;
}

.index-about-hotel__info-title {
  position: relative;
  width: fit-content;
}

.index-about-hotel__info-description {
  @include font-options(20px, 24px, 300);
}

.index-about-hotel__info-button {
  max-width: 230px;
}

.index-about-hotel__curtain {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  width: 100%;

  background: linear-gradient(to right, rgba($white, 0), rgba($white, 1) 30%);
}

@media (max-width: 1800px) {
  .index-about-hotel__container {
    padding: 120px 40px;
  }
}
@media (max-width: 1600px) {
  .index-about-hotel__container {
    padding: 120px 40px;
  }
  .index-about-hotel__info-description {
    font-size: 17px;
  }
  .index-about-hotel__info-wrapper {
    max-width: 650px;
  }
}
@media (max-width: 1600px) {
  .index-about-hotel {
    display: none;
  }
}
