@import "../../includes/elements/sections/decorative-section";
@import "../../includes/elements/about/about-more";
@import "../../includes/elements/call";


.index-about {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.index-about__wrapper {
  width: 100%;
  height: 100%;
  padding: 0;

  color: $white;

  background: linear-gradient(#74b0d6, #bda185);
}

.index-about__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  background: url("/assets/img/index/about-bg.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  transition: all .3s;
}

.index-about__description {
  width: 100%;
  height: 100%;
}

.index-about__more {
  flex-shrink: 0;
}

.index-about__pick-up-room-container {
  padding-top: 0;
  padding-bottom: 0;
  margin-top: calc(-90px - 57px);
}

.index-about__pick-up-room {
  // width: calc(100% - 270px);
  max-width: 1145px;
  // min-width: 742px;
  height: 90px;

  background-color: rgba($white, .2);
  backdrop-filter: blur(20px);
}

.index-about__aside {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  height: 100%;
}

.index-about__call {
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  width: 261px;
  height: 148px;

  background: linear-gradient(210.09deg, $black 3.55%, rgba($white, 0) 56.29%);
}

.index-about__curtain {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;

  background-color: $white;
}
.index-about__form {
  height: 100%;
  display: flex;
  align-items: center;
}
.index-about__form div {
  display: flex;
  // width: 241px;
  align-items: center;
  // margin-right: 20px;
  padding: 0 20px; 
  border-right:  1px solid #FFFFFF;
}
.index-about__form .lastDivChild {
  border-right: none;

}
.index-about__form input {
  background: none;
  border: none;
  color: #000000;
  font-size: 21px;
}
.index-about__form  :nth-child(1) {
  input {
    width: 95px;
    margin-right: 70px;
  }
}
.index-about__form  :nth-child(2) {
  input {
    width: 95px;
    margin-right: 70px;
  }
}
.index-about__form p {
  font-size: 18px;
  color: #000000;
  margin-right: 6px;
}
.index-about__form img {
  width: 25px;
  height: 22px;
}
.index-about__form-btn {
  font-size: 16px;
  width: 229px;
  height: 60px;
  color: #fff;
  background: #000;
  border: none;
}

@media (max-width: 1800px) {
  .index-about__pick-up-room-container {
   margin-left: -95px;
  }
  .index-about__pick-up-room {
    max-width: 1008px;
  }
  .index-about__form  :nth-child(1) {
    input {
      margin-right: 11px;
    }
    
  }
  .index-about__form  :nth-child(2) {
    input {
      margin-right: 11px;
    }
    
  }
  .index-about__form  :nth-child(3) {
    input {
      width: 168px;
      margin-right: 11px;
    }
  }
}
@media (max-width: 1576px) {
  .index-about__form-btn {
    width: 162px;
    height: 44px;
  }
  .index-about__form input {
    font-size: 16px;
  }
  .index-about__form  :nth-child(1) {
    input {
      margin-right: 3px;
      width: 75px;
    }
  }
    .index-about__form  :nth-child(2) {
      input {
        margin-right: 3px;
        width: 75px;
      }
    }
    .index-about__form  :nth-child(3) {
      input {
        margin-right: 3px;
        width: 130px;
      }
    }
    .index-about__pick-up-room {
      max-width: 828px;
    }
}

@media (max-width: 1400px) {
  .index-about__pick-up-room {
    max-width: 659px;
  }
  .index-about__form input {
    font-size: 12px;
  }
  .index-about__form p {
    font-size: 13px;
  }
  .index-about__form  :nth-child(1) {
    input {
      width: 64px;
    }
  }
  .index-about__form  :nth-child(3) {
    input {
      width: 97px;
    }
  }
  .index-about__form div {
    padding: 0 15px;
  }
  .index-about__form-btn {
    width: 120px;
    height: 44px;
  }
}
@media (max-width: 1200px) {
  .index-about__pick-up-room {
    max-width: 490px;
    // min-height: 65px;
  }
  .index-about__form div {
    border: none;
  }
  .index-about__form input {
    font-size: 12px;
  }
  .index-about__form p {
    font-size: 11px;
    margin-right: 0;
  }
  .index-about__form  :nth-child(1) {
    input {
      width: 55px;
    }
  }
  .index-about__form  :nth-child(2) {
    input {
      width: 55px;
    }
  }
  .index-about__form  :nth-child(3) {
    input {
      width: 82px;
    }
  }
  .index-about__form div {
    padding: 0 5px;
  }
  .index-about__form-btn {
    width: 93px;
    height: 29px;
    font-size: 11px;
  }
  .index-about__form img {
    width: 20px;
    height: 18px;
}
}

@media (max-width: 960px) {
  .index-about__aside {
    display: none;
  }
  .index-about__pick-up-room {
    margin: 0 auto;
  }
  .index-about__pick-up-room-container {
    width: 100%;
    margin-left: 0;
    padding: 0;
  }
}

@media (max-width: 525px) {
  .index-about {
    height: auto;
}
  
  .index-about__form {
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
  }
  .index-about__pick-up-room {
    background: none;
    backdrop-filter: none;
    height: 100%;
    // min-height: 200px;
  }
  .index-about__pick-up-room-container {
    padding: 0 15px;
    margin-left: 0;
    margin-top: 40px;
  }
  .index-about__form div {
    width: 100%;
    padding: 0 15px;
    margin-bottom: 35px;
  }
  .index-about__form  input {
    width: 100%!important;
    transform: translateX(-25px);
    font-size: 16px;
  }
  .index-about__form div {
    border: none;
    background: #fff;
  }
  .index-about__form-btn {
    width: 100%;
    height: 50px;
    margin-bottom: 20px;
    font-size: 14px;
    text-transform: uppercase;
  }
  .index-about__form p {
    transform: translate(-13px, -45px);
    font-size: 16px;
  }
  .index-about__pick-up-room-fon {
    position: absolute;
    top: -40px;
    left: 0;
    height: calc(100% + 40px);
    width: 148px;
    background-color: rgba(255,255,255,0.1);
    backdrop-filter: blur(20px);
  }
  .index-about__form img {
    width: 24px;
    height: 24px;
}
}




