.slider-controls {
  display: flex;
}

.slider-controls__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 111px;
  height: 106px;
  margin: 0;

  border: none;
  background-color: rgba($white, .7);
  backdrop-filter: blur(20px);
  cursor: pointer;

  &--prev {
    border-right: 1px solid $black;

    & .slider-controls__icon {
      transform: rotate(180deg);
    }
  }

  &:hover,
  &:focus {
    & .slider-controls__icon {
      &::before {
        transform: translateX(10px);
      }
      &::after {
        left: 50%;
        width: 27px;
      }
    }
  }

  &--disabled {
    cursor: default;
    pointer-events: none;

    & .slider-controls__icon {
      opacity: .3;
    }
  }

  &.js-isAnimateRun {
    pointer-events: none;
  }
}

.slider-controls__icon {
  position: relative;
  display: flex;
  align-items: center;
  height: 16px;
  margin: 0;
  padding: 0;

  &::before {
    display: block;
    font-size: 16px;
    color: $black;

    transition: all .4s;
  }

  &::after {
    position: absolute;
    content: "";
    top: 50%;
    left: calc(50% + 2px);
    width: 0;
    height: 2px;

    background-color: $black;
    transition: all .4s;
    transform: translate(-50%, -50%);
  }
}
