@import "../../includes/elements/slider-controls";


.index-awards {
  width: 100%;
}

.index-awards__container {
  display: flex;
  flex-wrap: wrap;
}

.index-awards__title {
  width: 100%;
  margin-bottom: 63px;
}

.index-awards__slider {
  overflow: hidden;
  max-width: 771px;

  border: 1px solid $sand;
  background-color: $white
}

.index-awards__list {
  @include list-style-none();
  display: flex;
}

.index-awards__item {
  flex-shrink: 0;
  width: 257px;
  padding: 20px 30px 30px;

  &:not(:last-child) {
    border-right: 1px solid $sand;
  }
}

.index-awards__logo-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 144px;
  height: 106px;
  margin: 0 auto 20px;
  overflow: hidden;
}

.index-awards__logo {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.index-awards__item-title {
  margin: 0;

  @include font-options(20px, 24px, 300);
  color: $grey;
}

.index-awards__slider-controls {
  align-self: flex-end;
}

@media (max-width: 1500px) {
  .index-awards__container {
    padding: 120px 20px;
  }
}
  @media (max-width: 1200px) {
    .index-awards__slider {
      max-width: 515px;
  }
}
@media (max-width: 562px) {
  .index-awards-contacts__awards {
    margin-bottom: 0px!important;
  }
  .index-awards {
    display: none;
  }
}