.index-about-more {
  @include list-style-none();

  display: flex;
  flex-direction: column;
  width: 270px;
  height: 100%;

  background-color: rgba($white, .1);
  backdrop-filter: blur(20px);
  border-left: 1px solid $white;
}

.index-about-more__item {
  display: block;
  width: 100%;
  height: 33.33%;
  margin: 0;


  &:not(:last-child) {
    border-bottom: 1px solid $white;
  }
}

.index-about-more__link {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 30px;

  @include font-options(18px, 22px, 500);
  color: $white;
  text-align: center;

  &:hover,
  &:focus {
    & .index-about-more__icon {
      transform: scale(1.2);
    }
  }

  &:active {
    opacity: .3;
  }
}

.index-about-more__icon {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70px;
  height: 70px;
  margin: auto auto 15px;

  background-color: $white;
  border-radius: 50%;
  transition: .4s;

  &::before {
    position: relative;
    z-index: 1;

    font-size: 19px;
    color: $black;
  }

  &--3d::before {
    font-size: 30px;
  }
}

@media (max-width: 1400px) {
  .index-about-more {
    width: 230px;
  }
  .index-about-more__item {
    display: flex;
    align-items: center;
  }
  .index-about-more__link {
    height: 50%
  }

}

@media (max-width: 1200px) {
  .index-about-more {
      width: 200px;
  }
}

@media (max-width: 960px) {
  .index-about__aside {
    display: none;
  }
}
