$circle-counter-size: 105px;


.circle-counter {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: $circle-counter-size;
  height: $circle-counter-size;
}

.circle-counter__container {
  width: 60%;
  height: 60%;
  overflow: hidden;
}

.circle-counter__list {
  @include list-style-none();
  width: 100%;
}

.circle-counter__item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: $circle-counter-size * .6;
  margin: 0;

  @include font-options(24px, 29px, 300);
}

.circle-counter__circle {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.circle-counter__circle-svg {
  width: 100%;
  height: 100%;
}

.circle-counter__circle-svg-line {
  stroke: $black;
}

.circle-counter__circle-svg-line-fill {
  stroke: $sand2;
}
