.title-h1 {
  @include font-options(36px, 43px, 500);
  margin: 0 0 30px;
}

.title-h2 {
  @include font-options(30px, 36px, 500);
  margin: 0 0 30px;
}

.title-h3 {
  @include font-options(26px, 31px, 500);
  margin: 0 0 22px;
}

.title-h4 {
  @include font-options(26px, 31px, 500);
  margin: 0 0 20px;
}

.title-h5 {
  @include font-options(22px, 26px, 500);
  margin: 0 0 16px;
}

.title-h6 {
  @include font-options(20px, 24px, 500);
  margin: 0 0 16px;
}

.sub-title {
  display: block;
  margin: 0 0 20px;

  @include font-options(24px, 29px, 500);
}

@mixin title-decorative() {
  display: block;
  margin: 0;

  @include font-options(288px, 345px, 400);
  font-family: "Cannu", "Times New Roman", serif;
  color: $sand;
  text-transform: lowercase;
}
