.call {
  display: flex;
  align-items: center;
  width: fit-content;
  padding: 20px;
  margin: 0;


  @include font-options(13px, 39px, 500);
  color: $white;
  text-transform: uppercase;
  letter-spacing: 0.25em;

  transition: transform .4s;

  // &:hover,
  // &:focus {
  //   transform: scale(1.3);
  // }

  // &:active {
  //   opacity: .3;
  // }
}

.call-icon {
  display: inline-block;
  width: 18px;
  height: 18px;
  margin-right: 13px;

  &::before {
    font-size: 18px;
    color: $white;
  }
}


.call:after {
  position: absolute;
    left: 50%;
	content: '';
	height: 40px;
	height: 2px;
    background: #fff;
    transition: all 0.5s linear;
    width: 0;
    bottom: 20px;  
}
.call:hover:after {
  width: 120px;
    margin-left: -60px;
}