.index-tripadvisor {
  background-color: $sand;
}

.index-tripadvisor__container {
  display: flex;
}

.index-tripadvisor__info {
  display: flex;
  flex-direction: column;
  width: 48%;
  padding: 0 93px 0 0;

  border-right: 1px solid $sand2;
}

.index-tripadvisor__intro {
  margin: 0 0 40px;
}

.index-tripadvisor__logo {
  width: 355px;
  height: 75px;
  margin: 0 0 12px;
  overflow: hidden;

  & img {
    width: 100%;
    height: auto;
    object-fit: contain;
  }
}

.index-tripadvisor__tagline {
  margin: 0;

  @include font-options(20px, 24px, 300);
}

.index-tripadvisor__villa-elena {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.index-tripadvisor__villa-elena-title {
  width: 100%;
  display: block;
  margin: 0 0 30px;

  @include font-options(48px, 58px, 500);
}

.index-tripadvisor__villa-elena-grade-text {
  margin: 0;
}

.index-tripadvisor__villa-elena-grade-stars {
  display: block;
  margin: 0;
}

.index-tripadvisor__star-icon {
  display: inline-block;
  vertical-align: middle;
  margin: 0 3px 14px 0;

  &::before {
    font-size: 28px;
    color: $black;
  }
}

.index-tripadvisor__villa-elena-grade-base {
  display: block;
  margin: 0;

  @include font-options(18px, 22px, 300);
}

.index-tripadvisor__villa-elena-rating {
  margin-top: auto;
}

.index-tripadvisor__villa-elena-rating-title {
  margin-bottom: 5px;
}

.index-tripadvisor__villa-elena-rating-text {
  margin: 0;
}

.index-tripadvisor__villa-elena-rating-label {
  @include font-options(64px, 77px, 500);
}


/*  reviews  */
.index-tripadvisor__reviews {
  width: 52%;
  padding: 0 0 0 93px;
}

.index-tripadvisor__reviews-title {
  margin-bottom: 10px;
}

.index-tripadvisor__reviews-list {
  @include list-style-none();
  margin: 0 0 17px;
}

.index-tripadvisor__reviews-item {
  display: block;
  padding: 20px 0;
  margin: 0;

  &:not(:last-child) {
    border-bottom: 1px solid $sand2;
  }
}

.index-tripadvisor__reviews-info {
  margin: 0 0 5px;

  @include font-options(20px, 24px, 300);
}

.index-tripadvisor__reviews-date {
  display: block;
  margin: 0 0 5px;

  @include font-options(14px, 17px, 300);
  color: $grey2;
}

.index-tripadvisor__reviews-more {
  display: block;
  margin: 0;

  @include font-options(16px, 24px, 500);
  color: $black;
  text-decoration: underline;

  &:hover,
  &:focus {
    color: $grey3;
  }

  &:active {
    opacity: .6;
  }
}

.index-tripadvisor__reviews-bottom {
  display: flex;
}

.index-tripadvisor__reviews-button {
  max-width: 230px;

  &:not(:last-child) {
    margin-right: 16px;
  }
}
.index-tripadvisor__villa-elena-subtitle {
  display: none;
}


@media (max-width: 1400px) {
  .index-tripadvisor__container {
    padding: 40px 20px;
  }
}

@media (max-width: 1200px) {
  .index-tripadvisor__logo {
    width: 235px;
  }
  .index-tripadvisor__reviews-button {
    padding: 16px 3px 15px;
    font-size: 14px;
  }
}

@media (max-width: 720px) {
  .index-tripadvisor__reviews {
    display: none;
  }
  .index-tripadvisor__info {
    padding: 0;
    border-right: none;
    width: 100%;
  }
  .index-tripadvisor__villa-elena-grade {
    display: none;
  }
  .index-tripadvisor__tagline {
    display: none;
  }
  .index-tripadvisor__villa-elena-rating-title {
    display: none;
  }
  .index-tripadvisor__info {
    flex-direction: column-reverse;
  }
  .index-tripadvisor__villa-elena-subtitle {
    display: block;
  }
  .index-tripadvisor__villa-elena-title {
    margin: 0;
    font-size: 32px;
  }
  .index-tripadvisor__villa-elena-rating-text {
    margin-bottom: 28px;
  }
  .index-tripadvisor__intro {
    margin: 0;
  }
  .index-tripadvisor__logo {
    margin: 0;
  }
}


@media (max-width: 411px) {
  .index-tripadvisor__villa-elena-title {
    font-size: 24px;
  }
  .index-tripadvisor__villa-elena-subtitle {
    font-size: 16px;
    margin: 0;
    margin-bottom: 20px;
  }
  .index-tripadvisor__villa-elena-rating-text {
    font-size: 24px;
  }
  .index-tripadvisor__villa-elena-rating-label {
    font-size: 35px;
  }
  .index-tripadvisor__logo {
    width: 165px;
    height: 35px;
  }
  .index-tripadvisor__container {
    padding: 26px 10px;
  }
}